import React from 'react';
import OneCategory from '../Category/OneCategory';

const RouterCategories = ({dataProducts, english, arabian, uzbek, russian}) => {
    return (
        <div>           
            <OneCategory dataProducts={dataProducts} english={english} russian={russian} uzbek={uzbek} arabian={arabian} white={false} />
        </div>
    );
};

export default RouterCategories;