import React from 'react';
import './Scss/Header.scss';
import { BrowserRouter, Link, Switch, Route  } from 'react-router-dom';
import Img1 from './images/back.png'
import Img2 from './images/product.png'
import Img3 from './images/flower.png'
import Img4 from './images/two-flower.png'
import { useTranslation } from 'react-i18next';

const Header = ({english, arabian, uzbek, russian}) => {

    const {t} = useTranslation()

    return (
        <div className='Header'>
            <div className='col-md-3 flower'>
                <img src={Img3} />
            </div>
            <div className='bg-creem'></div>
            <div className='col-md-3 two-flower'>
                <img src={Img4} />
            </div>
            <div className='wrapper'>
                <div className='row'>
                    <div className='col-md-6 header-img-top'>
                        <img className='back' src={Img1} />
                        <img className='prod' src={Img2} />
                    </div>
                    <div className='col-md-6 header-text'>
                        {/* {english &&
                            <>
                                <h1 className='favorite'>Favorite Mega snacks for your house, rest!</h1>
                                <p>Check out our production now!</p>
                                <div className='btn'>
                                    <a href='#spin' className='nav-link'>More info <i className='fa fa-arrow-right'></i></a>
                                </div>
                            </>
                        }
                        {russian &&
                            <>
                                <h1 className='favorite'>Favorite Mega snacks for your house, rest!</h1>
                                <p>Check out our production now!</p>
                                <div className='btn'>
                                    <a href='#spin' className='nav-link'>More info <i className='fa fa-arrow-right'></i></a>
                                </div>
                            </>
                        }
                        {uzbek &&
                            <>
                                <h1 className='favorite'>Favorite Mega snacks for your house, rest!</h1>
                                <p>Check out our production now!</p>
                                <div className='btn'>
                                    <a href='#spin' className='nav-link'>More info <i className='fa fa-arrow-right'></i></a>
                                </div>
                            </>
                        }
                        {arabian &&
                            <>
                                <h1 className='favorite'>Favorite Mega snacks for your house, rest!</h1>
                                <p>Check out our production now!</p>
                                <div className='btn'>
                                    <a href='#spin' className='nav-link'>More info <i className='fa fa-arrow-right'></i></a>
                                </div>
                            </>
                        } */}
                        <h1 className='favorite'>{t(17)}</h1>
                        <p>{t(18)}</p>
                        <div className='btn'>
                            <Link to={`/aboutus`} className='nav-link'>{t(16)} <i className='fa fa-arrow-right'></i></Link>
                        </div>
                    </div>
                    <div className='col-md-6 header-img'>
                        <img className='back' src={Img1} />
                        <img className='prod' src={Img2} />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Header;