import React, {useState} from 'react';
import AllBlogs from '../Blog/AllBlogs';
import Blog from '../Blog/Blog';


const JoinBlogs = ({id, name, img, text, date, english, arabian, uzbek, russian}) => {

    const [background, setBackground] = useState(false)
    function changeBack(item){
        setBackground(item)
    }

    return (
        <div>
            <AllBlogs id={id} name={name} image={img} text={text} date={date} english={english} russian={russian} uzbek={uzbek} arabian={arabian} />
            <Blog english={english} russian={russian} uzbek={uzbek} arabian={arabian} />          
        </div>
    );
};

export default JoinBlogs;