import React, { useState, useEffect, useRef } from 'react';
import './Scss/Spinner.scss';
import Img1 from './images/mega.png';
import Img2 from './images/scw.png';
import top from './images/top.png';
import bottom from './images/bottom.png';
import left from './images/left.png';
import right from './images/right.png';
import topLeft from './images/top-left.png';
import topRight from './images/top-right.png';
import bottomLeft from './images/bottom-left.png';
import bottomRight from './images/bottom-right.png';
import left1 from './images/left1.png';
import right1 from './images/right1.png';
import left2 from './images/left2.png';
import right2 from './images/right2.png';
import topLeft1 from './images/top-left1.png';
import topRight1 from './images/top-right1.png';
import bottomLeft1 from './images/bottom-left1.png';
import bottomRight1 from './images/bottom-right1.png';
import { BrowserRouter, Link, Switch, Route  } from 'react-router-dom';
import Audio from './audio/audio.mp3'
import ReactAudioPlayer from 'react-audio-player';

const Spinner = () => {


    let text = "|||||||||||||||||||||||||||||||||||||||||||||||||||||||||||||||||"
    const arc = 360
    let radius = window.innerWidth > 1600 ? 225 : 150
    if(window.innerWidth <= 500){
        radius = 100
        text = "||||||||||||||||||||||||||||||||||||||||"
    }

    const characters = text.split('');
    const degree = arc / characters.length;


    const [clicked, setClicked] = useState(false)

    let clickedHandle = false   

    let spans = document.querySelectorAll("#span")  
    // let counter = 0
    const [counter, setCounter] = useState(0)  

    let inCounter = document.querySelectorAll(".activating").length

    
    const audio = useRef(null)

    function handleSpin(item){
        setClicked(item)
        clickedHandle = item            
    }    

    const [touch, setTouch] = useState(false)    

    function handleTouch(item){
        setClicked(item)
        // handleSpin(item)
        setTouch(item)        
        if(item){
            audio.current.play()
        }else{
            setTimeout(() => {                        
                audio.current.pause()                    
            }, 500);
        } 
    }

    window.addEventListener("keydown", ({keyCode}) => {
        switch(keyCode){
            case 13: 
                handleSpin(true)
                break;
        }
    })

    window.addEventListener("keyup", ({keyCode}) => {
        switch(keyCode){
            case 13: 
                handleSpin(false)
                break;
        }
    })

    window.addEventListener("load", () => {
        handleSpin(false)
        clickedHandle = false
    })

    const canvas = useRef(null)
    const canvasNum = useRef(null)

    const [numberShow, setNumberShow] = useState(true)
    const [numberOne, setNumberOne] = useState(0)

    const [translated, setTranslated] = useState(0)
    
    useEffect(() => {        

        const ctx = canvas.current.getContext("2d")
        
        // 1093 934

        if(window.innerWidth > 1200){
            canvas.current.width = window.innerWidth
            canvas.current.height = window.innerHeight
        }else if(window.innerWidth <= 1200 && window.innerHeight > 740){
            canvas.current.width = window.innerWidth + 200 
            canvas.current.height = window.innerHeight - 100
        }else if(window.innerWidth <= 1200 && window.innerHeight <= 740){
            canvas.current.width = window.innerWidth + 100
            canvas.current.height = window.innerHeight
        }
        if(window.innerWidth <= 1020 && window.innerHeight > 740){
            canvas.current.width = window.innerWidth + 200
            canvas.current.height = window.innerHeight - 150
        }else if(window.innerWidth <= 1020 && window.innerHeight <= 740){
            canvas.current.width = window.innerWidth + 200
            canvas.current.height = window.innerHeight 
        }
        if(window.innerWidth <= 500 && window.innerHeight > 950){
            canvas.current.width = window.innerWidth + 500
            canvas.current.height = window.innerHeight - 500
        }else if(window.innerWidth <= 500 && window.innerHeight <= 950){
            canvas.current.width = window.innerWidth + 400
            canvas.current.height = window.innerHeight - 500 
        }
        if(window.innerWidth <= 500 && window.innerHeight <= 900){
            canvas.current.width = window.innerWidth + 400
            canvas.current.height = window.innerHeight - 400 
        }
        if(window.innerWidth <= 500 && window.innerHeight <= 790){
            canvas.current.width = window.innerWidth + 500
            canvas.current.height = window.innerHeight - 300 
        }

        if(window.innerWidth <= 435 && window.innerHeight > 950){
            canvas.current.width = window.innerWidth + 400
            canvas.current.height = window.innerHeight - 550
        }else if(window.innerWidth <= 435 && window.innerHeight <= 950){
            canvas.current.width = window.innerWidth + 400
            canvas.current.height = window.innerHeight - 500 
        }
        if(window.innerWidth <= 435 && window.innerHeight <= 900){
            canvas.current.width = window.innerWidth + 400
            canvas.current.height = window.innerHeight - 400 
        }
        if(window.innerWidth <= 435 && window.innerHeight <= 790){
            canvas.current.width = window.innerWidth + 500
            canvas.current.height = window.innerHeight - 300 
        }

        let width = canvas.current.width
        let height = canvas.current.height       
        
        // ctx.clearRect(0, 0, width, height)
        // ctx.save()  
        // ctx.translate(width/2, height/2) 

        class Products{
            constructor({x, y, img, w, h, c1, c2 ,color}){
                this.speed = 0.5

                this.position = {
                    x: x,
                    y: y
                }

                this.velocity = {
                    x: 0,
                    y: 0
                }
                
                this.image = new Image
                this.image.src = img
                this.count = 0

                this.width = w
                this.height = h
                this.translate = {
                    x: c1,
                    y: c2
                }     
                
                this.color = color

            }

            draw(){             
                ctx.drawImage(this.image, this.position.x - this.width/2, this.position.y - this.height/4, this.width, this.height)   
                // ctx.translate(this.translate.x, 0)                            
                // ctx.fillStyle = this.color               
                // ctx.fillRect(-width - this.width/2, this.position.y + this.translate.x, width, height)                         
            }
            update(){
                this.draw()
                if(clickedHandle){
                    this.position.x += this.translate.x 
                    this.position.y += this.translate.y                                     
                }
            }
        }

        let products = [new Products({x: width/2, y: height/8.5, img: top, w: width/20, h: width/13, c1: 0, c2: 0}), new Products({x: width/2, y: height/1.25, img: bottom, w: width/20, h: width/13, c1: 0, c2: 0}), 
                        new Products({x: width/3.05, y: height/2, img: left, w: width/13, h: width/20, c1: 0, c2: 0}), new Products({x: width/1.48, y: height/2, img: right, w: width/13, h: width/20, c1: 0, c2: 0}),
                        new Products({x: width/2.7, y: height/4, img: topLeft, w: width/15, h: width/15, c1: 0, c2: 0}), new Products({x: width/1.6, y: height/4, img: topRight, w: width/15, h: width/15, c1: 0, c2: 0}),
                        new Products({x: width/2.7, y: height/1.4, img: bottomLeft, w: width/15, h: width/15, c1: 0, c2: 0}), new Products({x: width/1.6, y: height/1.4, img: bottomRight, w: width/15, h: width/15, c1: 0, c2: 0}),
                        new Products({x: width/1.65, y: height/15, img: topRight1, w: width/25, h: width/18, c1: 0, c2: 0}), new Products({x: width/2.6, y: height/15, img: topLeft1, w: width/30, h: width/23, c1: 0, c2: 0}), 
                        new Products({x: width/1.7, y: height/1.13, img: bottomRight1, w: width/30, h: width/23, c1: 0, c2: 0}), new Products({x: width/2.55, y: height/1.13, img: bottomLeft1, w: width/25, h: width/18, c1: 0, c2: 0}),
                        new Products({x: width/1.4, y: height/3, img: right1, w: width/23, h: width/30, c1: 0, c2: 0}), new Products({x: width/1.4, y: height/1.5, img: right2, w: width/18, h: width/25, c1: 0, c2: 0}),
                        new Products({x: width/3.4, y: height/3, img: left1, w: width/18, h: width/25, c1: 0, c2: 0}), new Products({x: width/3.4, y: height/1.5, img: left2, w: width/23, h: width/30, c1: 0, c2: 0})]

        const animate = () => {

            requestAnimationFrame(animate) 

            ctx.fillStyle = "#21091F"
            ctx.fillRect(0, 0, width, height)  
            // ctx.restore() 
                        
            products.forEach(product => {
                product.update() 
                if(window.innerWidth > 500){
                    if(clickedHandle && product.velocity.x <= 65){                    
                        // product.velocity.x += 0.17  
                        audio.current.play()  
                    }else if(!clickedHandle && product.velocity.x > 1){                    
                        // product.velocity.x -= 0.17 
                        setTimeout(() => {                        
                            audio.current.pause()                    
                        }, 500);                                            
                    }       
                }else if(window.innerWidth <= 500){
                    if(clickedHandle && product.velocity.x <= 40){                    
                        // product.velocity.x += 0.17    
                        audio.current.play()                                   
                    }else if(!clickedHandle && product.velocity.x > 1){                    
                        // product.velocity.x -= 0.17  
                        setTimeout(() => {                        
                            audio.current.pause()                    
                        }, 500);
                    }       
                }     
                // window.addEventListener("load", () => {
                //     product.velocity.x = 0
                // })             
                // setCounter(Math.floor(product.velocity.x))
                    
            })

        } 


        animate()
        window.addEventListener("resize", animate)

        
    }, [canvas])  

    
    for(let i = 0; i < counter; i++){
        spans[i].classList.add("activating")
    }
    if(!clicked){
        for(let i = 0; i < counter; i++){
            spans[counter - 1].classList.remove("activating")
            spans[i].classList.contains("activating")
        }
    }  
    if(clicked && window.innerWidth > 500){
        let plusNumber = setInterval(() => {
            setNumberOne(numberOne + 1)
            setNumberShow(false)
            clearInterval(plusNumber)
        }, 6500);
    }else if(!clicked && window.innerWidth > 500){
        setTimeout(() => {   
            setNumberOne(0)         
            setNumberShow(true)
        }, 100);
    }
    if(clicked && window.innerWidth <= 500){
        let plusNumber = setInterval(() => {
            setNumberOne(numberOne + 1)
            setNumberShow(false)
            clearInterval(plusNumber)
        }, 4000);
    }else if(!clicked && window.innerWidth <= 500){
        setTimeout(() => {   
            setNumberOne(0)         
            setNumberShow(true)
        }, 100);
    }    



    return (
        <div className='Spinner' id='spinner'>
            <div className='wrapper'>                
                <a className={`cyrcle ${clicked && "activing"} ${!clicked && "touch"}`} onTouchStart={() => handleTouch(true)} onTouchEnd={() => handleTouch(false)} onMouseDown={() => handleTouch(true)} onMouseUp={() => handleTouch(false)} onMouseLeave={() => handleTouch(false)}>
                    <img src={Img2} />                                   
                    <audio ref={audio} src={Audio}>
                        {/* <source src={Audio} type="audio/mpeg"></source> */}
                    </audio>
                </a>
                <div className='text-border'>
                    <p>
                        {characters.map((char, i) => (
                            <span id='span' className={`${clicked && "activating"}`}
                            key={`heading-span-${i}`}
                            style={{                                        
                                height: `${radius}px`,
                                transform: `rotate(${degree * i - arc / 2}deg)`,
                                transformOrigin: `0 ${radius}px 0`,
                                transitionDelay: `${clicked == true? i * 0.1 : 0.1}s`
                            }}>
                            {char}
                            </span>
                        ))}
                    </p>
                </div>       
                <div className='canvas-main' style={{transform: `rotate(${clicked ? "3600" : "0"}deg)`} }>
                    <canvas className={`${clicked && "scale"}`} ref={canvas}></canvas>
                </div>     
                <div className={`number ${numberShow && "d-none"}`}>+{numberOne}</div>          
            </div>
        </div>
    );
}

export default Spinner;