import React from 'react';
import './Scss/Footer.scss';
import Img from './images/logo.png';
import Img1 from './images/coding.png';
import Img2 from './images/media.png';
import Img3 from './images/footer.png';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const Footer = ({english, arabian, uzbek, russian}) => {

    const {t} = useTranslation()

    return (
        <div className="Footer">
            <div className='wrapper'>
                <div className='f1'>                                        
                    <ul className={`footer-nav justify-content-between align-items-center`}>                           
                        <li className='nav-item'>
                            <a href="/#spinner" className='nav-link'>{t(23)}</a>
                        </li>
                        <li className='nav-item'>
                            <a href="/#products" className="nav-link">{t(4)}</a>
                        </li>
                        <li className='nav-item'>
                            <a href="/#blogs" className="nav-link">{t(20)}</a>
                        </li>
                        <li className='nav-item p-0  d-flex justify-content-center align-items-center'>                                    
                            <Link className="nav-link d-flex justify-content-center align-items-center p-0 w-75" to="/"><img className='w-100' src={Img} /></Link>                                    
                        </li>  
                        <li className='nav-item'>
                            <a href="/#contacts" className="nav-link">{t(2)}</a>
                        </li>  
                        <li className='nav-item'>
                            <a href="tel:+99895-004-80-90" className="nav-link d-flex justify-content-between align-items-center">
                                <i className="fa fa-phone"></i>
                                <ul>
                                    <li className={`nav-item numb`}>95-004-80-90</li>
                                    <li className={`nav-item numb`}>94-641-40-00</li>
                                </ul>
                            </a>
                        </li>                            
                    </ul>
                    <ul className={`footer-nav1`}>                                                           
                        <li className=''>
                            <a href="tel:+99895-004-80-90" className="nav-link d-flex justify-content-center align-items-center">
                                <i className="fa fa-phone"></i>
                                <ul>
                                    <li className={`nav-item numb`}>95-004-80-90</li>
                                    <li className={`nav-item numb`}>94-641-40-00</li>
                                </ul>
                            </a>
                        </li>    
                        <li className='img p-0'>
                            <a href="#contacts" className="nav-link d-flex justify-content-center align-items-center">
                                <Link className="nav-link nav-link d-flex justify-content-center align-items-center" to="/"><img className='w-1000' src={Img} /></Link>
                            </a>
                        </li>                          
                    </ul>                                                            
                    <div className='col-md-12'>
                        <div className='row'>
                            <div className='col-md-4'>
                                <h6>{t(14)}</h6>
                            </div>
                            <div className='col-md-5'>
                                <h6>{t(15)}</h6>
                                <img className='abba' src={Img1} />
                                <img className='abba' src={Img2} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>            
        </div>
    );
};

export default Footer;