import React, { useState } from 'react';
import './Scss/Category.scss';
import { BrowserRouter, Route, Link, Switch } from 'react-router-dom';
import Fade from 'react-reveal/Fade'
import Zoom from 'react-reveal/Zoom'
import axios from 'axios';
import Img1 from './images/back.png'
import Img2 from './images/IMAGE (9).png'
import { useTranslation } from 'react-i18next';

const Category = ({english, arabian, uzbek, russian}) => {

    const {t} = useTranslation()

    return ( 
        <div className='Category'>
            <div className='wrapper'>
                <div className='col-md-12 other'>
                    {/* {english &&
                        <h1>Other Categries</h1>
                    }
                    {russian &&
                        <h1>Другие Kатегории</h1>
                    }
                     {uzbek &&
                        <h1>Boshqa Toifalar</h1>
                    }*/}
                    <h1>{t(7)}</h1>
                </div>
                <div className='row'>
                    <div className='col-md-6'>
                        <Link className='nav-link' to='/category/1'>
                            <img src={Img1} />
                            {english &&
                                <h5>Categries 2</h5>
                            }
                            {russian &&
                                <h5>Categries 2</h5>
                            }
                            {uzbek &&
                                <h5>Categries 2</h5>
                            }                            
                        </Link>
                    </div>
                    <div className='col-md-6'>
                        <Link className='nav-link' to='/category/2'>
                            <img src={Img2} />
                            {english &&
                                <h5>Categries 3</h5>
                            }
                            {russian &&
                                <h5>Categries 3</h5>
                            }
                            {uzbek &&
                                <h5>Categries 3</h5>
                            }                            
                        </Link>
                    </div>
                </div>
            </div>
        </div>
     );
}

export default Category;