import React, { useState } from 'react';
import './Scss/Contacts.scss';
import { Link } from 'react-router-dom';
import Img1 from './images/image 126.png'
import IntlTelInput from 'react-intl-tel-input';
import 'react-intl-tel-input/dist/main.css';
import { useTranslation } from 'react-i18next';

const Contacts = ({english, arabian, uzbek, russian, changeModal, word}) => {

    const { t } = useTranslation()

    const [nameValue, setNameValue] = useState("")
    const [numberValue, setNumberValue] = useState("")
    const [value, setValue] = useState("")    

    const [redName, setRedName] = useState(false);
    const [redNumber, setRedNumber] = useState(false)
    const [redText, setRedText] = useState(false)

    function handleChange1(item){
        setNameValue(item)
        setRedName(false)
    }
    function handleChange2(item){
        setNumberValue(item)
        setRedNumber(false)
    }
    function handleChange3(item){
        setValue(item)
        setRedText(false)
    }
    
    let bot = {
        TOKEN: "5458070162:AAG7Q0XROYbx4vi2EDr4eW72X5RPnBFPI20",
        chatID: "-1001726663965",
        message: `Ism: ${nameValue} Raqam: ${numberValue} Xabar: ${value}`
    }

    function sendMessage(e){  
        
        if(nameValue === ""){            
            setRedName(true)
        }else if(numberValue === ""){           
            setRedNumber(true)
        }else if(value === ""){            
            setRedText(true)
        }else{
            fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
                method : "GET"
            })
            .then(success => {
                console.log('send message')
            }, error => {
                console.log(error)
            })

            setNameValue("")
            setNumberValue("")
            setValue("")
            changeModal()
        }
        
    }

    return (
        <>
            <div className='Contacts' id="contacts">
                <div className='wrapper'>
                    <div className='row algin-items-center justify-content-center'>
                        <div className='col-md-8 message row justify-content-center'> 
                            {/* {english &&                                
                                <h1 className='any'>Any questions?</h1>                           
                            }
                            {russian &&                                
                                <h1 className='any'>Любые вопросы?</h1>                           
                            }
                            {uzbek &&                                
                                <h1 className='any'>Savollaringiz bormi?</h1>                           
                            }
                            {arabian &&                                
                                <h1 className='any'>أي أسئلة؟</h1>                           
                            } */}
                            <h1 className='any'>{t(8)}</h1>
                            <div className='col-md-6 p-1 position-relative'>
                                {/* {english &&
                                    <label className='form-label' htmlFor='name'>Name</label>
                                }
                                {russian &&
                                    <label className='form-label' htmlFor='name'>Имя</label>
                                }
                                {uzbek &&
                                    <label className='form-label' htmlFor='name'>Ism</label>
                                }
                                {arabian &&
                                    <label className='form-label' htmlFor='name'>اسم</label>
                                } */}
                                <label className='form-label' htmlFor='name'>{t(9)}</label>
                                <input className={`${redName && "red-line"} form-control`} type="text" value={nameValue} onChange={(e) => handleChange1(e.target.value)} id='name' placeholder='Abdulhamid' />
                                <i className='fa fa-user-o'></i>
                            </div>
                            <div className='col-md-6 p-1 position-relative' onChange={(e) => handleChange2(e.target.value)}>
                                {/* {english &&
                                    <label className='form-label d-block' htmlFor='number'>Phone Number</label>
                                }
                                {russian &&
                                    <label className='form-label d-block' htmlFor='number'>Телефонный Hомер</label>
                                }
                                {uzbek &&
                                    <label className='form-label d-block' htmlFor='number'>Telefon Raqami</label>
                                }
                                {arabian &&
                                    <label className='form-label d-block' htmlFor='number'>رقم الهاتف</label>
                                } */}
                                <label className='form-label d-block' htmlFor='number'>{t(10)}</label>
                                <IntlTelInput preferredCountries={['uz']} containerClassName="intl-tel-input" value={numberValue} placeholder='+99894 123 45 67' inputClassName={`form-control tel-control w-100 ${redNumber && "red-line"}`} id="number" />
                                <i className='fa fa-mobile'></i>
                            </div>
                            <div className='col-md-12 p-1 mt-3'>
                                {/* {english &&
                                    <label className='form-label' htmlFor='question'>Question</label>
                                }
                                {russian &&
                                    <label className='form-label' htmlFor='question'>Вопрос</label>
                                }
                                {uzbek &&
                                    <label className='form-label' htmlFor='question'>Savol</label>
                                }
                                {arabian &&
                                    <label className='form-label' htmlFor='question'>سؤال</label>
                                } */}
                                <label className='form-label' htmlFor='question'>{t(11)}</label>
                                <textarea className={`${redText && "red-line"} form-control`} rows="6" id='question' value={value} onChange={(e) => handleChange3(e.target.value)} placeholder='Even if you have not started your project yet'></textarea>
                            </div>
                            <div className='col-md-3 p-1'>
                                {/* {english && 
                                    <button className='btn btn-group' onClick={() => sendMessage()}>Send</button>
                                }
                                {russian && 
                                    <button className='btn btn-group' onClick={() => sendMessage()}>Отправлять</button>
                                }
                                {uzbek && 
                                    <button className='btn btn-group' onClick={() => sendMessage()}>Yuborish</button>
                                }
                                {arabian && 
                                    <button className='btn btn-group' onClick={() => sendMessage()}>يرسل</button>
                                } */}
                                <button className='btn btn-group' onClick={() => sendMessage()}>{t(13)}</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Contacts;