import React from 'react';
import Blog from '../Blog/Blog';
import Category from '../Category/Category';
import Header from '../Header/Header';
import Product from '../Products/Products';
import Spinner from '../Spinner/Spinner';

const RouterHeader = ({dataProducts, english, arabian, uzbek, russian}) => {
    return (
        <div className='RouterHeader'>
            <Header english={english} russian={russian} uzbek={uzbek} arabian={arabian} />
            <Spinner />
            <Product dataProducts={dataProducts} english={english} russian={russian} uzbek={uzbek} arabian={arabian} white={false} />
            <Category english={english} russian={russian} uzbek={uzbek} arabian={arabian} />
            <Blog english={english} russian={russian} uzbek={uzbek} arabian={arabian} />
        </div>
    );
};

export default RouterHeader;