import React, { useState } from 'react';
import './Scss/About.scss';
import { BrowserRouter, Link } from 'react-router-dom';
import Img1 from './images/IMAGE (8).png'
import Img2 from './images/IMAGE (9).png'
import Img3 from '../Header/images/flower.png'
import Img4 from '../Header/images/two-flower.png'
import Blog from '../Blog/Blog';
import Video from './video/Munis2.mp4'
import videojs from 'video.js';
import 'video.js/dist/video-js.css';
import { useTranslation } from 'react-i18next';

const About = ({ english, uzbek, russian }) => {

    const {t} = useTranslation()

    const options = {
        autoplay: false,
        controls: true,
        responsive: true,
        fluid: true,
        sources: [{
          src: Video,
          type: 'video/mp4'
        }]
      };
    
      const onReady = (player) => {
        playerRef.current = player;
    
        // You can handle player events here, for example:
        player.on('waiting', () => {
          player.log('player is waiting');
        });
    
        player.on('dispose', () => {
          player.log('player will dispose');
        });
      };
    
        const videoRef = React.useRef(null);
        const playerRef = React.useRef(null); 
    
        React.useEffect(() => {
    
            // Make sure Video.js player is only initialized once
            if (!playerRef.current) {
            const videoElement = videoRef.current;        
    
            if (!videoElement) return;
    
            const player = playerRef.current = videojs(videoElement, options, () => {
                player.log('player is ready');
                onReady && onReady(player);
            });
    
            // You can update player in the `else` block here, for example:
            } else {
            // const player = playerRef.current
            // player.autoplay(options.autoplay);
            // player.src(options.sources);
            }
        }, [options, videoRef]);        
    
        // Dispose the Video.js player when the functional component unmounts
        React.useEffect(() => {
            const player = playerRef.current;
    
            return () => {
            if (player) {
                player.dispose();
                playerRef.current = null;
            }
            };
        }, [playerRef]);

    return (
        <>
            <div className={`About`}>
                <div className='wrapper1'>
                    <div className='header-images'>
                        <div className='col-md-3 flower'>
                            <img src={Img3} />
                        </div>
                        <div className='bg-creem'></div>
                        <div className='col-md-3 two-flower'>
                            <img src={Img4} />
                        </div>
                    </div>
                    <div className='col-md-12'>
                        {/* <video loop className="video">
                            <source src={Video} type="video/mp4" />    
                        </video>
                         */}
                         <div className='video'>
                            <div data-vjs-player>
                                <video ref={videoRef} className='video-js vjs-big-play-centered' />
                            </div>
                         </div>
                        <h1 className={`name`}>{t(21)}</h1>
                        {english &&
                            <h5>Our company was founded in 2017. It has been producing a variety of snacks since the day it started. You can find all the types of snacks you want at Mega. You will not go astray by buying the best quality and delicious of worms, pistachios, etc. from us. Make your day  more fun with mega snacks!</h5>                    
                        }
                        {russian &&
                            <h5>Наша компания основана в 2017 году. С самого начала работы компания производит разнообразные снеки. Вы можете найти все виды снеков, которые вы хотите в Mega. Вы не ошибётесь, купив у нас самые качественные и вкусные курут, фисташки и т.д. Ваш день еще веселее со снеками Mega!</h5>
                        }
                        {uzbek &&
                            <h5>Bizning kompaniyamiz 2017-yilda tashkil topgan. Ish boshlagan kundan buyon sneklarning turli xillarini ishlab chiqaradi. O'zingiz istagan snek turining barchasini Megadan topa plasiz. Qurt, pista, va boshqalarining eng sifatli va mazalisini bizdan xarid qilib adashmaysiz. Mega sneklari bilan kuningiz yanada maroqli!</h5>
                        }
                    </div>
                </div>
            </div>
            <Blog english={english} russian={russian} uzbek={uzbek} />
        </>
    );
};

export default About;