import React, { useEffect, useState } from 'react';
import './Scss/Blog.scss';
import { BrowserRouter, Route, Link, Switch } from 'react-router-dom';
import Slider from 'react-slick';
import Img1 from './images/IMAGE (8).png'
import Img2 from './images/IMAGE (9).png'
import Img3 from './images/IMAGE (10).png'
import Img4 from './images/middle.png'
import Img5 from './images/IMAGE (9).png'
import Img6 from './images/back.png'
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import AllBlogs from './AllBlogs';
import Navbar from '../Navbar/Navbar';
import Fade from 'react-reveal/Fade'
import Zoom from 'react-reveal/Zoom'
import axios from 'axios';
import { useTranslation } from 'react-i18next';

const Blog = ({white, show, english, uzbek, russian}) => {       

    const [dataSlider, setDataSlider] = useState([]);

    const urlBlogs = `http://93.189.40.27:8100/blogs/`;

    useEffect(() => {
        async function fetchData() {
        const request = await axios.get(urlBlogs);
        setDataSlider(request.data);
        return request;
        };
        fetchData()
    }, [urlBlogs])


    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3
    };           

    const [left, setLeft] = useState(1);
    const [middle, setMiddle] = useState(2);
    const [right, setRight] = useState(3);

    function middle1(){
        if(left === 1 && middle === 2){            
            setLeft(2)
            setMiddle(1)
        }else if(left === 1 && right === 2){            
            setLeft(2)
            setRight(1)
        }      
        
        if(left === 3 && middle === 2){            
            setLeft(2)
            setMiddle(3)
        }else if(left === 3 && right === 2){            
            setLeft(2)
            setRight(3)
        } 
    }
    function middle2(){
        if(middle === 1 && left === 2){            
            setLeft(1)
            setMiddle(2)
        }else if(middle === 1 && right === 2){            
            setMiddle(2)
            setRight(1)
        }      
        
        if(middle === 3 && left === 2){            
            setLeft(3)
            setMiddle(2)
        }else if(middle === 3 && right === 2){            
            setMiddle(2)
            setRight(3)
        } 
    }
    function middle3(){
        if(right === 1 && middle === 2){            
            setRight(2)
            setMiddle(1)
        }else if(right === 1 && left === 2){            
            setLeft(1)
            setRight(2)
        }      
        
        if(right === 3 && middle === 2){            
            setRight(2)
            setMiddle(3)
        }else if(right === 3 && left === 2){            
            setLeft(3)
            setRight(2)
        } 
    }

    const {t} = useTranslation()

    return (                      
            <div className={`Blog ${white && "bg-darkcyan"}`} id="blogs" > 
                <div className='wrapper'>                   
                    <Fade left>
                        <div className={`row ${white && "text-white"} ${show && "d-none"}`}>
                            <div className={`col-md-4 ${white && "text-white"} ${show && "d-none"}`}>
                                {/* {english &&
                                    <h1 className={`usefull ${white && "text-white"} ${show && "d-none"}`}>Some tips on sunflower sees</h1>
                                }
                                {russian &&
                                    <h1 className={`usefull ${white && "text-white"} ${show && "d-none"}`}>Несколько советов по семечкам подсолнуха</h1>
                                }
                                {uzbek &&
                                    <h1 className={`usefull ${white && "text-white"} ${show && "d-none"}`}>Kungaboqar urug'lari bo'yicha ba'zi maslahatlar</h1>
                                }    */}
                                <h1 className={`usefull ${white && "text-white"} ${show && "d-none"}`}>{t(22)}</h1>                             
                            </div>                            
                        </div>
                    </Fade>
                    <Fade>
                        <div className={`slider mt-5 ${show && "d-none"}`}>
                            <div className='slider-carousel-0'>
                                {dataSlider.map((item) => {
                                    return(
                                        <div className='col-md-4'>
                                            <img className='blog-img' src={item.image} />
                                            {english &&
                                                <h5 className={white && "text-white"}>{item.name_en}</h5>
                                            }
                                            {russian &&
                                                <h5 className={white && "text-white"}>{item.name_ru}</h5>
                                            }
                                            {uzbek &&
                                                <h5 className={white && "text-white"}>{item.name_uz}</h5>
                                            }                                            
                                            <div className="d-flex">
                                                <a className={`view-all ${white && "white-all"}`}>
                                                    <Link className='nav-link d-flex align-items-center justify-content-between' to={`/all-blogs/${item.id}`}>
                                                        {english &&
                                                            "View Blog"
                                                        }
                                                        {russian &&
                                                            "Посмотреть блог"
                                                        }
                                                        {uzbek &&
                                                            "Blogni ko'rish"
                                                        }                                                       
                                                        <i className='fa fa-arrow-right'></i>
                                                    </Link> 
                                                </a>
                                            </div>
                                        </div>
                                    )
                                })}
                            </div>
                            <Slider className="slider-carousel" {...settings}>
                                {dataSlider.map((item) => {
                                    return(
                                        <div className='col-md-4'>
                                            <img className='blog-img' src={item.image} />
                                            {english &&
                                                <h5 className={white && "text-white"}>{item.name_en}</h5>
                                            }
                                            {russian &&
                                                <h5 className={white && "text-white"}>{item.name_ru}</h5>
                                            }
                                            {uzbek &&
                                                <h5 className={white && "text-white"}>{item.name_uz}</h5>
                                            }                                            
                                            <div className="d-flex">
                                                <a className={`view-all ${white && "white-all"}`}>
                                                    <Link className='nav-link d-flex align-items-center justify-content-between' to={`/all-blogs/${item.id}`}>
                                                        {english &&
                                                            "View Blog"
                                                        }
                                                        {russian &&
                                                            "Посмотреть блог"
                                                        }
                                                        {uzbek &&
                                                            "Blogni ko'rish"
                                                        }                                                       
                                                        <i className='fa fa-arrow-right'></i>
                                                    </Link> 
                                                </a>
                                            </div>
                                        </div>
                                    )
                                })}
                            </Slider>                      
                        </div>
                    </Fade>
                    {/* <div className={`videos ${youtube && "d-none"}`}>
                        <div className="col-md-12 mt-5">
                            {english &&
                                <h4 className={white && "text-white"}>Some videos about our company on YouTube</h4>
                            }
                            {russian &&
                                <h4 className={white && "text-white"}>Несколько видео о нашей компании на YouTube</h4>
                            }
                            {uzbek &&
                                <h4 className={white && "text-white"}>YouTube'da kompaniyamiz haqida ba'zi videolar</h4>
                            }
                            {arabian &&
                                <h4 className={white && "text-white"}>بعض مقاطع الفيديو حول شركتنا على موقع يوتيوب</h4>
                            }
                        </div>
                        <Zoom>
                            <div className="some">                        
                                <div className='col-6 youtube'>
                                    <div className={`${left === 1 && "left"} ${left === 2 && "middle"} ${left === 3 && "right"}`} onClick={() => middle1()}>
                                        <img className='img-left' src={Img5} />
                                        <div className='contast w-100'>                                    
                                            <p>Brand New Flour Company, USA</p>
                                            <i className='fa fa-play'></i>
                                        </div>
                                    </div>
                                </div>                                                     
                                <div className='col-6 youtube'>
                                    <div className={`${middle === 1 && "left"} ${middle === 2 && "middle"} ${middle === 3 && "right"}`} onClick={() => middle2()}>
                                        <img className='img-middle' src={Img4} />
                                        <div className='contast w-100'>                                    
                                            <p>Brand New Flour Company, Uzbekistan</p>
                                            <i className='fa fa-play'></i>
                                        </div>
                                    </div>
                                </div>                                                         
                                <div className='col-6 youtube'>
                                    <div className={`${right === 1 && "left"} ${right === 2 && "middle"} ${right === 3 && "right"}`} onClick={() => middle3()}>
                                        <img className='img-right' src={Img6} />
                                        <div className='contast w-100'>                                    
                                            <p>Brand New Flour Company, Russian</p>
                                            <i className='fa fa-play'></i>
                                        </div>
                                    </div>
                                </div>                                                        
                            </div>
                        </Zoom>
                    </div> */}
                </div>
            </div>                    
    );
};

export default Blog;